* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial Black', 'Helvetica Neue', Arial, sans-serif;
  background-color: #f8f8f8;
  color: #000;
  line-height: 1.4;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding-top: 80px;
}

/* Header Styles */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background: #fff;
  z-index: 1000;
  border-bottom: 2px solid #000;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 42px;
  font-weight: 900;
  letter-spacing: -2px;
  color: #000;
  text-decoration: none;
  font-style: italic;
  transform: skew(-5deg);
}

.nav-menu {
  display: flex;
  gap: 40px;
  list-style: none;
}

.nav-menu a {
  text-decoration: none;
  color: #000;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: color 0.3s ease;
}

.nav-menu a:hover {
  color: #ff0000;
}

.menu-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.menu-toggle span {
  width: 25px;
  height: 3px;
  background: #000;
  margin: 3px 0;
  transition: 0.3s;
}

/* Triangle Logo Component */
.triangle-logo {
  width: 80px;
  height: 80px;
  position: relative;
  margin: 0 auto 20px;
}

.triangle {
  width: 0;
  height: 0;
  border-left: 40px solid transparent;
  border-right: 40px solid transparent;
  border-bottom: 69px solid;
  position: absolute;
}

.triangle.red { border-bottom-color: #ff0000; }
.triangle.blue { border-bottom-color: #0066ff; }
.triangle.black { border-bottom-color: #000; }
.triangle.white { border-bottom-color: #fff; }

.triangle-text {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  font-size: 10px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Home Page */
.home {
  text-align: center;
  padding: 60px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.hero-section {
  margin-bottom: 80px;
}

.hero-title {
  font-size: 120px;
  font-weight: 900;
  font-style: italic;
  letter-spacing: -5px;
  margin-bottom: 40px;
  transform: skew(-5deg);
}

.hero-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  margin-top: 60px;
}

.hero-card {
  background: #fff;
  border: 3px solid #000;
  padding: 40px 20px;
  text-decoration: none;
  color: #000;
  transition: all 0.3s ease;
  position: relative;
}

.hero-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}

.hero-card h3 {
  font-size: 24px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 20px;
}

/* Product Grid */
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  padding: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.product-card {
  background: #fff;
  border: 2px solid #000;
  overflow: hidden;
  transition: all 0.3s ease;
  text-decoration: none;
  color: #000;
}

.product-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
}

.product-image {
  width: 100%;
  height: 300px;
  background: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #999;
}

.product-info {
  padding: 20px;
}

.product-name {
  font-size: 18px;
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.product-price {
  font-size: 16px;
  font-weight: 700;
  color: #ff0000;
}

/* Page Headers */
.page-header {
  text-align: center;
  padding: 60px 20px 40px;
  background: #000;
  color: #fff;
  margin-bottom: 40px;
}

.page-title {
  font-size: 60px;
  font-weight: 900;
  font-style: italic;
  letter-spacing: -2px;
  transform: skew(-5deg);
}

/* Content Sections */
.content-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.section-title {
  font-size: 36px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 30px;
  text-align: center;
}

/* Buttons */
.btn {
  display: inline-block;
  padding: 15px 30px;
  background: #000;
  color: #fff;
  text-decoration: none;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
}

.btn:hover {
  background: #ff0000;
  transform: translateY(-2px);
}

.btn-outline {
  background: transparent;
  color: #000;
  border: 2px solid #000;
}

.btn-outline:hover {
  background: #000;
  color: #fff;
}

/* Footer */
.footer {
  background: #000;
  color: #fff;
  padding: 60px 20px 20px;
  margin-top: 80px;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.footer-section h4 {
  font-size: 18px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.footer-section ul {
  list-style: none;
}

.footer-section ul li {
  margin-bottom: 10px;
}

.footer-section ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.3s ease;
}

.footer-section ul li a:hover {
  color: #ff0000;
}

.social-links {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.social-links a {
  color: #fff;
  font-size: 24px;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: #ff0000;
}

.footer-bottom {
  text-align: center;
  padding-top: 40px;
  border-top: 1px solid #333;
  margin-top: 40px;
  color: #999;
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-menu {
    display: none;
  }
  
  .menu-toggle {
    display: flex;
  }
  
  .hero-title {
    font-size: 60px;
  }
  
  .page-title {
    font-size: 36px;
  }
  
  .logo {
    font-size: 32px;
  }
  
  .product-grid {
    grid-template-columns: 1fr;
    padding: 20px;
  }
  
  .hero-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-up {
  animation: fadeInUp 0.6s ease-out;
}

/* Cart Styles */
.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.cart-total {
  text-align: right;
  padding: 20px;
  font-size: 24px;
  font-weight: 900;
}

/* Form Styles */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: 700;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 14px;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 15px;
  border: 2px solid #000;
  background: #fff;
  font-size: 16px;
  font-weight: 500;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #ff0000;
}

/* Loading States */
.loading {
  text-align: center;
  padding: 60px 20px;
  font-size: 18px;
  color: #999;
}

.spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #000;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}